* {
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
}
:root {
  --white: #fff;
  --silver: #c1c1c1;
  --dull-grey: #707070;
  --wet-asphalt_1: #4a4a4a;
  --wet-asphalt_2: #484848;
  --grey-brown: #343434;
  --BondiBeachWaters: #03a0cb;
}
body {
  background-color: var(--white);
  font-size: 1rem;
  font-weight: normal;
  line-height: normal;
  color: var(--grey-brown);
  font-family: 'Open Sans', Arial, Helvetica, sans-serif;
  overflow-x: hidden;
  box-shadow: 0px 0px 0.5rem 0px rgba(0,0,0,0.75);
  min-height: 100vh;
}
a {
  text-decoration: none;
  cursor: pointer;
  color: var(--grey-brown);
  font-size: 1rem;
}
a:hover {
  opacity: 0.8;
}
b,
.title {
  font-weight: 700;
}
h1 {
  font-size: 2.25rem;
}
h2 {
  font-size: 2rem;
}
h3 {
  font-size: 1.75rem;
}
h4 {
  font-size: 1.5rem;
}
h5 {
  font-size: 1.25rem;
}
h6 {
  font-size: 1.125rem;
}
p {
  font-size: 1rem;
}
ul,
ol {
  list-style: none;
}
.container {
  max-width: 90rem;
  width: 100%;
  margin: 0 auto;
}
.container .wrapper {
  padding: 0 0.5rem;
}
header .header__top {
  padding: 1.125rem 0;
  position: relative;
}
header .header__top .wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
header .header__bottom {
  position: relative;
  box-shadow: 0px 0px 0.5rem 0px rgba(0,0,0,0.75);
}
header .header__bottom ul {
  width: 100%;
  display: flex;
  align-items: center;
}
header .header__bottom ul li {
  flex: 1;
  height: 6rem;
  position: relative;
}
header .header__bottom ul li:before {
  content: '';
  position: absolute;
  display: block;
  left: 50%;
  transform: translateX(-50%);
  bottom: 0;
  width: 0;
  height: 0.5rem;
  transition: 0.125s linear;
  background: #64d4b8;
  background: linear-gradient(to right, #64d4b8 0%, #64d4b8 31%, #2895bd 70%, #137e9e 100%);
}
header .header__bottom ul li.active:before {
  width: 100%;
}
header .header__bottom ul li:hover:before {
  width: 100%;
  opacity: 0.8;
}
header .header__bottom ul li a {
  padding: 1.5rem 0.5rem;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
header .header__nav ul {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  grid-column-gap: 2.75rem;
}
header .header__nav ul li {
  display: flex;
  align-items: center;
}
header .header__nav ul li a {
  width: 100%;
}
header .header__nav ul li p {
  width: 100%;
  text-align: center;
}
header .header__nav--v1 > :first-child {
  display: none;
}
header .header__nav--v2 ul li {
  display: none;
}
header .header__nav--v2 ul > :last-child {
  display: block;
}
.topSlider {
  overflow: hidden;
  width: 100%;
  display: block;
  position: relative;
}
.topSlider__backGround {
  position: absolute;
  z-index: -1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  filter: blur(0.75rem);
  opacity: 0.7;
  width: 100%;
}
.slider .slide {
  overflow: hidden;
  position: relative;
}
.slider .slide img {
  position: absolute;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1.1, 1.1);
}
.slider .slide .container {
  display: flex;
  justify-content: flex-end;
  padding: 0;
}
.slider .slide__info {
  box-shadow: 0px 0px 0.5rem 0px rgba(0,0,0,0.75);
  background-color: rgba(193,193,193,0.4);
  backdrop-filter: blur(0.5rem);
  padding: 1.5rem 3rem;
  color: var(--white);
  padding: 1.5rem 3rem;
  z-index: 1;
  width: calc(100% * 1 / 3);
  height: calc(89rem * 0.3234);
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
}
.slider .slide__info__title {
  margin-bottom: 0.063rem;
}
.slider .slide__info__description {
  margin: 1rem 0 2rem;
}
.slick-dots {
  width: calc(100% * 1 / 3);
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: 0.5rem;
  left: 50%;
  transform: translateX(-50%);
}
.slick-dots li {
  flex: 1;
  width: 100%;
  max-width: 1rem;
  position: relative;
  margin: 0.5rem;
  border-radius: 50%;
}
.slick-dots li:hover button {
  background-color: var(--BondiBeachWaters);
  background: linear-gradient(to right, #64d4b8 0%, #64d4b8 31%, #2895bd 70%, #137e9e 100%);
  transform: rotate(45deg);
}
.slick-dots li.slick-active:hover button {
  background-color: var(--BondiBeachWaters);
  background: linear-gradient(to right, #64d4b8 0%, #64d4b8 31%, #2895bd 70%, #137e9e 100%);
  transform: rotate(45deg);
}
.slick-dots li.slick-active button {
  background-color: var(--white);
}
.slick-dots li:before {
  content: '';
  width: 100%;
  display: block;
  padding-top: 100%;
}
.slick-dots li button {
  cursor: pointer;
  border-radius: 50%;
  position: absolute;
  background-color: transparent;
  border: 0.125rem solid var(--white);
  transition: 0.5s ease-in-out;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  font-size: 0px;
}
.btn {
  width: 9rem;
  height: 2.25rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  letter-spacing: 1px;
  font-weight: 700;
  color: var(--white);
  border-radius: 0.25rem;
  border: 1px solid var(--white);
  transition: 0.5s ease-in-out;
}
.btn--default {
  background: #64d4b8;
  background: linear-gradient(to right, #64d4b8 0%, #64d4b8 31%, #2895bd 70%, #137e9e 100%);
}
.btn--default:hover {
  background: linear-gradient(to left, #64d4b8 0%, #64d4b8 31%, #2895bd 70%, #137e9e 100%);
}
.btn--more {
  background: var(--silver);
  backdrop-filter: blur(0.5rem);
}
